.card {
    background: #fff;
    border-radius: 2px;
    height: 250px;
    margin: 1rem;
    position: relative;
    width: 325px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
}
  
.card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
}
.card > .img-container > img {
    width: 60%
}
  
.card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
}
.wrapper {
    padding-top: 50px;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
}